.header_main
{
  font-size: 200%;
}
.header_ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: rgb(0, 80, 117);
  }
  
  .header_li {
    float: left;
  }
  
  .header_li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  .header_li a:hover   {
    background-color: rgb(6, 117, 168);
  }
  .header_main a.active{
    background-color: rgb(6, 117, 168);

  }

  
  .header_anchorIcon
  {
    position: absolute;
    /* margin:1%; */
    right: 1%;
    top:1%;
  }

  .dropdown-content {
    display: none;
    position: fixed;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
  }
  
  .dropdown-content a:hover {background-color: navy;}
  
  .dropdown:hover .dropdown-content {
    display: block;
  }
